.categoryBadge {
  background-color: red !important;
  color: white !important;
  border-radius: 50%;
  height: 75px;
  width: 75px;
  margin-top: -18px !important;
  margin-left: -18px !important;
  position: absolute !important;
  z-index: 999 !important;
}

.badgeTitle {
  display: flex;
  font-size: 11px;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.badgeTitle > p {
  margin: 0px;
  width: 70px;
  white-space: normal;
  text-align: center;
  user-select: none;
}